<template>
    <div class="container-xxl flex-grow-1 container-p-y">
        <h4 class="fw-bold py-3 mb-4">
            <span class="text-muted fw-light">Usuario /</span> Perfil
        </h4>
        <div class="row">
            <div class="col-md-12">
                <div class="card mb-4">
                    <h5 class="card-header">Detalles del perfil</h5>
                    <!-- Account -->
                    <div class="card-body">
                        <div class="d-flex align-items-start align-items-sm-center gap-4">
                            <img
                                src="https://www.csj.gob.sv/wp-content/uploads/2021/06/cropped-logocsj-1-180x180.png"
                                alt="user-avatar"
                                class="d-block rounded"
                                height="100"
                                width="100"
                                id="uploadedAvatar"
                            />
                            <div class="button-wrapper">
                                <label for="upload" class="btn btn-primary me-2 mb-4 disabled" tabindex="0" >                                    
                                    <span class="d-none d-sm-block">Subir nueva foto</span>
                                    <i class="bx bx-upload d-block d-sm-none"></i>
                                    <input
                                        type="file"
                                        id="upload"
                                        class="account-file-input"
                                        hidden
                                        accept="image/png, image/jpeg"
                                        disabled
                                    />
                                </label>
                                <button type="button" class="btn btn-outline-secondary account-image-reset mb-4" disabled>
                                    <i class="bx bx-reset d-block d-sm-none"></i>
                                    <span class="d-none d-sm-block"> Cancelar</span>
                                </button>

                                <p class="text-muted mb-0">Permitido JPG o PNG. Tamaño máximo de 800K</p>
                            </div>
                        </div>
                    </div>
                    <hr class="my-0" />
                    <div class="card-body">
                        <form id="formAccountSettings" method="POST" onsubmit="return false">
                        <div class="row">
                            <div class="mb-3 col-md-6">
                                <label class="fw-semibold d-block" for="caso-fecha-hecho">Usuario</label>
                                <input
                                    class="form-control"
                                    type="text"
                                    v-model="store.state.USUARIO.usuario"
                                    autofocus
                                />
                            </div>
                            <div class="mb-3 col-md-6">
                            <label for="lastName" class="form-label">Correo</label>
                            <input class="form-control" type="text" v-model="store.state.USUARIO.correo" readonly/>
                            </div>
                        </div>
                        <div class="mt-2">
                            <button type="submit" class="btn btn-primary me-2"> <i class="bx bx-save"></i> Guardar</button>
                            <button type="reset" class="btn btn-outline-secondary"> <i class="bx bx-reset"></i> Cancelar</button>
                        </div>
                        </form>
                    </div>
                    <!-- /Account -->
                    </div>
                    <div class="card">
                    <h5 class="card-header">Desactivar Cuenta</h5>
                    <div class="card-body">
                        <div class="mb-3 col-12 mb-0">
                        <div class="alert alert-warning">
                            <h6 class="alert-heading fw-bold mb-1">¿Estás segura/o de que quieres desactivar tu cuenta?</h6>
                            <p class="mb-0">Una vez que desactivar tu cuenta, no hay vuelta atrás. Por favor, esté seguro.</p>
                        </div>
                        </div>
                        <form id="formAccountDeactivation" onsubmit="return false">
                        <div class="form-check mb-3">
                            <input
                            class="form-check-input"
                            type="checkbox"
                            name="accountActivation"
                            id="accountActivation"
                            />
                            <label class="form-check-label" for="accountActivation">Confirmo la desactivación de mi cuenta</label>
                        </div>
                        <button disabled type="submit" class="btn btn-danger deactivate-account"> <i class="bx bx-x-circle bx-sm"></i> Desactivar cuenta</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import store from '@/store/index'
import router from '@/router'

export default{
    mounted() {
        if(localStorage.getItem('access_token') === null){
            store.commit('headerSesion');
            router.push({ name: "acceso"});
        }else
            store.commit('headerSesionOk');
    },    
    setup(){
        return {
            store
        }
    }
}
</script>
